import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from './layout.component';
import { AuthGuard } from '../../guards/auth.guard';

const routes: Routes = [
  
  {
    path: '',
    component: LayoutComponent,
    children: [
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
      {

        path: 'dashboard',
        loadChildren: () =>
          import('../dashboard/dashboard-module').then(
            (m) => m.DashboardModule
          ),
      },
      {
        path: 'users',
        loadChildren: () =>
          import('../users/users-module').then((m) => m.UsersModule),
      },
      {
        path: 'employees',
        loadChildren: () =>
          import('../employees/employees-module').then(
            (m) => m.EmployeesModule
          ),
      },
      {
        path: 'profile',
        loadComponent: () =>
          import('../profile/profile.component').then(
            (m) => m.ProfileComponent
          ),
      },
      {
        path: 'departments',
        loadChildren: () =>
          import('../departments/departments.module').then(
            (m) => m.DepartmentsModule
          ),
      },
      {
        path: 'tasks',
        loadChildren: () =>
          import('../tasks/tasks-module').then((m) => m.TasksModule),
      },
      {
        path: 'partners',
        loadChildren: () =>
          import('../partners/partners-module').then((m) => m.PartnersModule),
      },
      {
        path: 'visits',
        loadChildren: () =>
          import('../visits/visits-module').then((m) => m.VisitsModule),
      },
      {
        path: 'travel',
        loadChildren: () =>
          import('../travel/travel.module').then((m) => m.TravelModule),
      },
      {

        path: 'meetings',
        loadChildren: () =>
          import('../meetings/meetings.module').then((m) => m.MeetingsModule),
      },
      {
        path: 'letters',
        loadChildren: () =>
          import('../letters/letters.module').then((m) => m.LettersModule),
      },
      {
        path: 'documents',
        loadChildren: () =>
          import('../documents/documents.module').then((m) => m.DocumentsModule),
      },
      {
        path: 'workflow',
        loadChildren: () =>
          import('../workflow/workflow.module').then((m) => m.WorkflowModule),
      },
      // {
      //   path: '',
      //   redirectTo: 'dashboard',
      //   pathMatch: 'full',
      // },
    ],
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LayoutRoutingModule {}
